import React from 'react'
import SupervisorSidebar from './SupervisorSidebar';
import { useState, useEffect, useRef } from 'react';
import { faTrash } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import ReactPaginate from 'react-paginate';


const SupervisorViewTopic = () => {
  const [data, setData] = useState([])
  const [userData, setuserData] = useState('')

  const [limit, setLimit] = useState(5)
  const currentPage = useRef();
  const [pageCount, setPageCount] = useState(1)

  useEffect(() => {

    currentPage.current = 1;
    getTopics();
    getUserDetails();



  }, [])


  function getUserDetails() {
    fetch("http://localhost:4000/user/data", {
      method: "POST",
      crossDomian: true,
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
        "Access-Control-Allow-Origin": "*"
      },
      body: JSON.stringify({
        token: window.localStorage.getItem("token"),
      }),
    }).then((res) => res.json())
      .then((data) => {
        console.log(data, "User Data");
        setuserData(data.data);



        if (data.data === "token expired") {
          alert("Token expired Login again")
          window.localStorage.clear();
          window.location.href = "/sign-in"
        }



      })

  }

  function getTopics() {

    fetch(`http://localhost:4000/supervisor/gettopics?page=${currentPage.current}&limit=${limit}`, {
      method: "GET",
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data, "Data");
        setData(data.result)

        setPageCount(data.pageCount)
      })
  }


  const deleteTopic = (topicId, title) => {
    console.log(topicId, title, userData.email);

    if (window.confirm(`Are you sure you wante to remove ${title}`)) {


      fetch("http://localhost:4000/supervisor/deletetopic", {
        method: "POST",
        crossDomian: true,
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
          "Access-Control-Allow-Origin": "*"
        },
        body: JSON.stringify({
          topicId: topicId,
          email: userData.email

        }),
      }).then((res) => res.json())
        .then((data) => {
          console.log(data.data);
          alert(data.data);
          getTopics();


        })
    }
  }



  const handlePageClick = (e) => {

    currentPage.current = e.selected + 1;
    getTopics()



  }

  function changeLimit() {
    currentPage.current = 1;
    getTopics();
  }


  return (
    <div className='app'>
      <div className="col-2 p-0">
        <SupervisorSidebar />
      </div>
      <div className="col-10 p-0">
        <div>

          <div><label>List of Topics</label></div>
          <table className="table table-sm table-bordered">
            <tbody>
              <tr>
                <th className="table-active">Topics</th>
                <th className="table-active">Description</th>
                <th className='table-active'>Supervisor</th>
                <th className='table-active'>Remove</th>
              </tr>
              {data.map(m => (
                <tr key={m._id}>
                  <td>{m.title} </td>
                  <td>{m.description} </td>
                  <td>{m.supervisor}</td>
                  <td>
                    <FontAwesomeIcon icon={faTrash} style={{ cursor: ' pointer' }} onClick={() => deleteTopic(m._id, m.title, m.email)} ></FontAwesomeIcon>
                  </td>

                </tr>
              ))}
            </tbody>
          </table>

          <div className="d-flex justify-content-center" >
            <ReactPaginate
              breakLabel="..."
              nextLabel="next >"
              onPageChange={handlePageClick}
              pageRangeDisplayed={5}
              pageCount={pageCount}
              previousLabel="< previous"
              renderOnZeroPageCount={null}
              marginPagesDisplayed={2}
              containerClassName="pagination justify-content-center"
              pageClassName="page-item"
              pageLinkClassName="page-link"
              previousClassName="page-item"
              previousLinkClassName="page-link"
              nextClassName="page-item"
              nextLinkClassName="page-link"
              activeClassName="active"
              forcePage={currentPage.current - 1}
            />

            <div className="p-1">
              <input className="limit-width" placeholder="limit" onChange={e => setLimit(e.target.value)} />
              <button className="btn btn-primary" onClick={changeLimit}>Set</button>
            </div>
          </div>

        </div>
      </div>
    </div>
  )
}

export default SupervisorViewTopic
