import React, { useEffect } from 'react'
import 'bootstrap/dist/css/bootstrap.css';
import SupervisorSidebar from './SupervisorSidebar';

import { useState } from 'react';


const AddTopic = () => {

    const [topic, setTopic] = useState("");
    const [description, setDescription] = useState("");

    const [userData, setuserData] = useState('')
    const [superviser, setSupervisor] = useState("")
    const [admin, setAdmin] = useState('')


    useEffect(() => {

        fetch("http://localhost:4000/user/data", {
            method: "POST",
            crossDomian: true,
            headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
                "Access-Control-Allow-Origin": "*"
            },
            body: JSON.stringify({
                token: window.localStorage.getItem("token"),
            }),
        }).then((res) => res.json())
            .then((data) => {
                console.log(data, "User Data");
                if (data.data.userType === "Supervisor") {
                    setSupervisor("Supervisor")
                }
                else if (data.data.userType === "Admin") {
                    setAdmin("Admin")
                }
                setuserData(data.data);
                if (data.data === "token expired") {
                    alert("Token expired Login again")
                    window.localStorage.clear();
                    window.location.href = "/sign-in"
                }



            })


    }, [])

    const handleSubmit = (e) => {
        e.preventDefault();
        console.log(topic, description, userData.email, userData.firstName + " " + userData.surName);
        fetch("http://localhost:4000/supervisor/uploadtopic", {
            method: "POST",
            crossDomian: true,
            headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
                "Access-Control-Allow-Origin": "*"
            },
            body: JSON.stringify({
                name: userData.firstName + " " + userData.surName,
                title: topic,
                description: description,
                email: userData.email
            }),
        }).then((res) => res.json())
            .then((data) => {
                if (data.status === "Topic updated") {
                    console.log(data, "Topic Uploaded");
                    alert("Topic updated")
                    setTopic("");
                    setDescription("");

                }

            })



            .catch((err) => {
                console.error(err);
            });
    };

    return (
        <div className='app' >
            <div className="col-2 p-0">

                <SupervisorSidebar />
            </div>
            <div className="col-8 p-0 m-3">
                <form className='justify-content-center' onSubmit={handleSubmit}   >

                    <div className="form-floating mb-3">
                        <textarea value={topic} onChange={(e) => setTopic(e.target.value)} type="text" className="form-control" id="floatingInput" placeholder="Enter the topic"></textarea>
                        <label >Topic title</label>
                    </div>

                    <div className="form-floating">
                        <textarea value={description} onChange={(e) => setDescription(e.target.value)} type="text" className="form-control" id="floatingInput" placeholder="Description"></textarea>
                        <label htmlFor="floatingPassword">Description</label>
                    </div>
                    <button type="submit" className="btn btn-primary mt-2">Upload Topic</button>
                </form >


            </div>
        </div >
    )
}

export default AddTopic
